table {
  width: 100%;
}

.table-th {
  background-color: #eee;
}

.table-td,
.table-th {
  padding: 10px 20px;
  border: 1px solid #ddd;
  vertical-align: middle;
}

.current {
  color: white;
}

/* new csss */

.ant-menu-horizontal > .ant-menu-item a {
  color: rgb(0 0 0 / 65%);
}
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item:hover::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu:hover::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-active::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-active::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-open::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-open::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-item-selected::after,
.ant-menu-horizontal:not(.ant-menu-dark) > .ant-menu-submenu-selected::after {
  display: none;
}

.ant-layout-header .header-nav li {
  display: inline-block;
}

.ant-layout-header .header-nav .ant-menu-inline-block {
  background: transparent;
  box-shadow: none;
}

.ant-layout-header .header-nav .ant-menu-inline-block a {
  color: rgba(0, 0, 0, 0.65);
}

ul.ant-menu-item-group-list li a {
  padding: 6px 12px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 400;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: #314659;
}
ul.ant-menu-item-group-list li a:hover {
  color: #314659;
}

ul.ant-menu-item-group-list li a.active {
  background-color: #eee;
  color: #314659;
}

html,
body {
  height: auto;
}

.codetext {
  border-radius: 8px;
  background: #f6f6f6 !important;
  text-shadow: none !important;
}

.mb-3 {
  margin-bottom: 1rem;
}

.alert-copy-code {
  position: absolute;
  width: 100%;
}
.align-itam-in-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.site-card-border-less-wrapper {
  padding: 30px;
  background: #ececec;
}
